export default {
	data() {
		return {
			scrollHeight:'500px',
			leftArray:[],
			mainArray:[],
			topArr:[],
			leftIndex:0,
			scrollInto:''
		}
	},
	onLoad(){
		uni.getSystemInfo({
			success:(res)=>{
				/* 设置当前滚动容器的高，若非窗口的高度，请自行修改 */
				this.scrollHeight=`${res.windowHeight}px`;
			}
		});
	},
	mounted(){
		this.getListData();
	},
